import { Component, EventEmitter, OnInit, OnDestroy, Output, Input } from '@angular/core';

@Component({
  selector: 'lib-modal-radio',
  templateUrl: './modal-radio.component.html',
  styleUrls: ['./modal-radio.component.scss']
})
export class ModalRadioComponent implements OnInit, OnDestroy {
  @Input() displayModalRadio: boolean;
  @Input() title: string;
  @Input() topContentText: string;
  @Input() warningMessage: string;
  @Input() hideCloseBtn: boolean;
  @Input() hideConfirmBtn: boolean;
  @Input() closeBtn: string;
  @Input() confirmBtn: string;
  @Input() modalClass: string;
  @Input() confirmBtnClass: string;
  @Input() closeBtnClass: string;
  @Input() preSelectedIndex: number;
  @Input() radioList: Array<{}>;
  @Input() radioLabels: Array<{label: string, key: string}>;
  @Output() selectionChanged: EventEmitter<number> = new EventEmitter();
  @Output() selectionClosed: EventEmitter<boolean> = new EventEmitter();

  selectedIndex: number;

  ngOnInit(): void {
    this.selectedIndex = this.preSelectedIndex;
  }

  onClose() {
    this.selectedIndex = this.preSelectedIndex;
    this.selectionClosed.emit(false);
  }

  onSubmit() {
    this.selectionChanged.emit(this.selectedIndex);
  }

  ngOnDestroy() {
    this.selectionChanged.unsubscribe();
    this.selectionClosed.unsubscribe();
  }

  setSelected(i) {
    this.selectedIndex = i;
  }
}
