import {Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import listPlugin from '@fullcalendar/list';
import moment from 'moment';
import allLocales from '@fullcalendar/core/locales-all';

@Component({
  selector: 'lib-full-calendar',
  templateUrl: './full-calendar.component.html',
  styleUrls: ['./full-calendar.component.scss']
})
export class FullCalendarComponent implements OnInit {

  @Input() events: any[];
  @Output() eventClick = new EventEmitter<any>();
  @Output() dateClick = new EventEmitter<any>();
  @Output() eventDrop = new EventEmitter<any>();
  @Output() eventResize = new EventEmitter<any>();
  @Output() dateRender = new EventEmitter<any>();
  @Input() dayCalander: boolean;
  @Input() weekCalander: boolean;
  @Input() slotDuration = '00:15:00';
  @Input() minTime = '06:00:00';
  @Input() maxTime = '20:00:00';
  @Input() scrollTime = '08:00:00';
  @Input() editable = true;
  @Input() snapMinutes = 15;
  @Input() businessHours = true;
  @Input() firstHour = true;
  @Input() timeFormat = 'H:mm';
  options: any;
  // tslint:disable-next-line:variable-name
  private _calendarApi: any;
  @ViewChild('calendar') calendar: any;

  constructor() {
  }

  ngOnInit() {
    this.options = {
      plugins: [dayGridPlugin, timeGridPlugin, interactionPlugin, listPlugin],
      defaultView: this.viewCalendar(),
      defaultDate: moment().format('YYYY-MM-DD'),
      locales: allLocales,
      locale: 'fr',
      slotDuration: this.slotDuration,
      allDaySlot: false,
      minTime: this.minTime,
      maxTime: this.maxTime,
      scrollTime: this.maxTime,
      snapMinutes: this.snapMinutes,
      businessHours: this.businessHours,
      firstHour: this.firstHour,
      timeFormat: this.timeFormat,
      header: {
        right: 'today, prev,next',
        center: 'title',
        left: 'dayGridMonth,timeGridWeek,timeGridDay',
      },
      editable: this.editable,
      dateClick: this.datatClickA.bind(this),
      eventClick: this.eventClickB.bind(this),
      eventResize: this.eventResizeA.bind(this),
      eventDrop: this.eventDropA.bind(this),
      datesRender: this.dateRenderA.bind(this),
    };
  }

  eventDropA(event) {
    this.eventDrop.emit(event);
  }

  eventResizeA(event) {
    this.eventResize.emit(event);
  }

  datatClickA(event) {
    this.dateClick.emit(event);
  }

  eventClickB(event) {
    this.eventClick.emit(event);
  }

  dateRenderA(event) {
    this.dateRender.emit(event);
  }

  viewCalendar() {
    if (this.dayCalander) {
      return 'timeGridDay';
    }
    if (this.weekCalander) {
      return 'timeGridWeek';
    }
    return 'dayGridMonth';
  }

  get calendarApi() {
    this._calendarApi = this.calendar.getApi();
    return this._calendarApi;
  }
}
