import { Component, Input, Output, EventEmitter } from '@angular/core';

import { SelectItem } from 'primeng/api';

import { generateGuid } from '../../shared/guid';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'anef-input-radio-two-way-binding',
  templateUrl: './input-radio-two-way-binding.component.html',
  styleUrls: ['./input-radio-two-way-binding.component.scss'],
})
export class InputRadioTwoWayBindingComponent {
  @Input() model: any;
  @Input() name: string;
  @Input() required = false;
  @Input() disabled = false;
  @Input() label: string;
  @Input() column = false;
  @Input() spread = false;
  @Input() options: SelectItem[];
  @Output() ngModelChange = new EventEmitter();
  @Input() set errors(errors: string | Array<string>) {
    if (!errors) {
      this.error = null;
    } else if (typeof(errors) === 'string') {
      this.error = [errors];
    } else {
      this.error = errors;
    }
  }

  guid = generateGuid();
  error = null;
}
