import { Component, Input, Output, EventEmitter } from '@angular/core';

import { SelectItem } from 'primeng/api';

import { generateGuid } from '../../shared/guid';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'anef-input-check-two-way-binding',
  templateUrl: './input-check-two-way-binding.component.html',
  styleUrls: ['./input-check-two-way-binding.component.scss'],
})
export class InputCheckTwoWayBindingComponent {
  @Input() model: any;
  @Input() name: string;
  @Input() required = false;
  @Input() readonly = false;
  @Input() label: string;
  @Input() option: SelectItem;
  @Input() errors = [];
  @Output() ngModelChange = new EventEmitter();

  guid = generateGuid();

  get icon(): string {
    return this.option.icon || 'pi pi-check';
  }
}
