import {Component, EventEmitter, Input, OnDestroy, Output} from '@angular/core';

@Component({
  selector: 'lib-information-modal',
  templateUrl: './information-modal.component.html',
  styleUrls: ['./information-modal.component.css']
})
export class InformationModalComponent implements OnDestroy {
  @Input() displayInfo: boolean;
  @Input() title: string;
  @Input() titleCheck: string;
  @Input() text: string;
  @Input() facultativeText: string;
  @Input() labelBtn: string;
  @Input() textBold: string;
  @Input() hideIconBloc = false;
  @Output() displayInfoChange = new EventEmitter();

  constructor() {}

  onClose() {
    this.displayInfoChange.emit(false);
  }

  ngOnDestroy() {
    this.displayInfoChange.unsubscribe();
  }
}
