import { Component, EventEmitter, Input, OnDestroy, Output } from '@angular/core';

@Component({
  selector: 'lib-error-modal',
  templateUrl: './error-modal.component.html',
  styleUrls: ['./error-modal.component.css']
})
export class ErrorModalComponent   implements OnDestroy  {
  @Input() displayError: boolean;
  @Input() title: string;
  @Input() titleCheck: string;
  @Input() text: string;
  @Input() facultativeText: string;
  @Input() labelBtn: string;
  @Input() textBold: string;
  @Output() displayErrorChange = new EventEmitter();

  constructor() { }


  onClose() {
    this.displayErrorChange.emit(false);
  }

  ngOnDestroy() {
    this.displayErrorChange.unsubscribe();
  }
}
