// Angular
import { Component, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';

export interface MultiSelectOption {
  label: string;
  value: {
    id: number,
    name: string,
    code: string,
    icon?: string
  };
}

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'input-multi-select',
  templateUrl: './input-multi-select.component.html',
  styleUrls: ['./input-multi-select.component.scss'],
})

export class InputMultiSelectComponent {
  @Input() formGroup: FormGroup;
  @Input() controlName: string;
  @Input() label: string;
  @Input() option: MultiSelectOption[];
  @Input() filter = false;
  @Input() maxSelectedItemsLabel = '{0} éléments sélectionnés';
  @Input() defaultLabel = 'Choisir';
}
