import { Component, Input, Output, EventEmitter } from '@angular/core';

import { SelectItem } from 'primeng/api';
import { isEqual } from 'lodash';

import { generateGuid } from '../../shared/guid';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'anef-input-multi-select-two-way-binding',
  templateUrl: './input-multi-select-two-way-binding.component.html',
  styleUrls: ['./input-multi-select-two-way-binding.component.scss'],
})
export class InputMultiSelectTwoWayBindingComponent {
  error = null;

  @Input('model') set model(value: any) {
    this.value = value || [];
  }
  @Input() required = false;
  @Input() readonly = false;
  @Input() label: string;
  @Input() placeholder: string;
  @Input() options: SelectItem[] = [];
  @Input() url: string;
  @Input() filter = false;
  @Input() limit = Infinity;
  @Input() set errors(errors: string | Array<string>) {
    if (!errors) {
      this.error = null;
    } else if (typeof(errors) === 'string') {
      this.error = [errors];
    } else {
      this.error = errors;
    }
  }
  @Input() addItemLabel: string;
  @Output() ngModelChange = new EventEmitter();

  guid = generateGuid();
  value: any[] = [];

  onModelChange(): void {
    this.ngModelChange.emit(this.value);
  }

  addItem(): void {
    this.value.push(null);
  }

  deleteItem(index: number): void {
    this.value.splice(index, 1);
  }

  toString(value: any): string {
    const selectedOption = this.options.find(option => isEqual(value, option.value));
    return (selectedOption && selectedOption.label) ? selectedOption.label : '-';
  }
}
