import { OnInit } from '@angular/core';
// Angular
import { Component, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { SelectItem } from 'primeng/api';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'anef-input-check',
  templateUrl: './input-check.component.html',
  styleUrls: ['./input-check.component.scss'],
})
export class InputCheckComponent implements OnInit {
  @Input() formGroup: FormGroup;
  @Input() controlName: string;
  @Input() name: string;
  @Input() required = false;
  @Input() readonly = false;
  @Input() disableCheckbox = false;
  @Input() label: string;
  @Input() option: SelectItem;
  @Input() binary = false;
  @Input() messages = {
    required: 'Ce champ est obligatoire'
  };

  ngOnInit(): void {
    if (!this.name) {
      this.name = this.controlName;
    }
    if (this.disableCheckbox) {
      this.formGroup.controls[this.controlName].disable();
    }
  }
  get icon(): string {
    return this.option.icon || 'pi pi-check';
  }
  public formContainsError(): boolean {
    return this.formGroup.controls[this.controlName].errors !== null &&
    (this.formGroup.controls[this.controlName].dirty || this.formGroup.controls[this.controlName].touched);
  }

  toString() {
    return this.option.label;
  }
}
