import { Component, EventEmitter, Input, OnDestroy, Output, ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'lib-success-modal',
  templateUrl: './success-modal.component.html',
  styleUrls: ['./success-modal.component.css']
})
export class SuccessModalComponent implements OnDestroy  {
  @Input() displaySuccess: boolean;
  @Input() title: string;
  @Input() titleCheck: string;
  @Input() text: string;
  @Input() facultativeText: string;
  @Input() labelBtn: string;
  @Input() textBold: string;
  @Output() displaySuccessChange = new EventEmitter();


  constructor() { }

  onClose() {
    this.displaySuccessChange.emit(false);
  }

  ngOnDestroy() {
    this.displaySuccessChange.unsubscribe();
  }
}
