import { Component, Input, Output, EventEmitter } from '@angular/core';

import { generateGuid } from '../../shared/guid';

interface Icon {
  type: string;
  class: string;
}

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'anef-input-text-two-way-binding',
  templateUrl: './input-text-two-way-binding.component.html',
  styleUrls: ['./input-text-two-way-binding.component.scss']
})
export class InputTextTwoWayBindingComponent {
  private readonly MAX_LENGTH: number = 524288;
  error = null;

  @Input() model: any;
  @Input() type = 'text';
  @Input() required = false;
  @Input() readonly = false;
  @Input() capitalize = false;
  @Input() label: string;
  @Input() placeholder: string;
  @Input() maxLength: number = this.MAX_LENGTH;
  @Input() set errors(errors: string | Array<string>) {
    if (!errors) {
      this.error = null;
    } else if (typeof(errors) === 'string') {
      this.error = [errors];
    } else {
      this.error = errors;
    }
  }
  @Input() icon: Icon;
  @Input() disabled = false;
  @Output() ngModelChange = new EventEmitter();

  guid = generateGuid();

  onModelChange(event: string) {
    if (this.capitalize) {
      event = event.charAt(0).toUpperCase() + event.slice(1);
    }
    this.ngModelChange.emit(event);
  }

  toString(): string {
    return this.model ? this.model : '-';
  }
}
