import { Component, Input, Output, EventEmitter } from '@angular/core';


@Component({
  // tslint:disable-next-line: component-selector
  selector: 'anef-input-switch-two-way-binding',
  templateUrl: './input-switch-two-way-binding.component.html',
  styleUrls: ['./input-switch-two-way-binding.component.scss']
})
export class InputSwitchTwoWayBindingComponent {

  @Input() model: boolean;
  @Input() disabled: boolean;
  @Input() choice1: string;
  @Input() choice2: string;
  @Input() customStyleClass = 'anef-switch';
  @Input() label: string;
  @Output() change: EventEmitter<any> = new EventEmitter();


  public isChecked(): boolean {
    return this.model;
  }

  public isDisabled(): boolean {
    return this.disabled;
  }

  onchange(event) {
    if (this.change) {
      this.change.emit(event);
    }
  }
}
