import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { generateGuid } from '../../shared/guid';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'anef-input-date-two-way-binding',
  templateUrl: './input-date-two-way-binding.component.html',
  styleUrls: ['./input-date-two-way-binding.component.scss'],
})
export class InputDateTwoWayBindingComponent implements OnInit {
  @Input() set model(model: Date | string) {
    if (typeof model === 'string') {
      if (!model || this.showTime) {
        this.modelDate = new Date(model);
      } else {
        model = new Date(model);
        this.modelDate = new Date(model.getUTCFullYear(), model.getUTCMonth(), model.getUTCDate());
      }
    } else {
      if (!model || this.showTime) {
        this.modelDate = model;
      } else {
        this.modelDate = new Date(model.getUTCFullYear(), model.getUTCMonth(), model.getUTCDate());
      }
    }
  }
  @Input() label: string;
  @Input() required = false;
  @Input() readonly = false;
  @Input() disabled = false;
  @Input() minDateValue: Date;
  @Input() maxDateValue: Date;
  @Input() showTime = false;
  @Input() helpText = '';
  @Input() showCheckbox = false;
  @Input() checkboxModel = false;
  @Input() checkboxLabel: string;
  @Input() disableDateOnCheck = false;
  @Input() set errors(errors: string | Array<string>) {
    if (!errors) {
      this.error = null;
    } else if (typeof(errors) === 'string') {
      this.error = [errors];
    } else {
      this.error = errors;
    }
  }

  @Output() ngModelChange = new EventEmitter<Date>();
  @Output() checkboxValueChanged: EventEmitter<boolean> = new EventEmitter();

  modelDate: Date;
  fr: any;
  disableDateFromCheck: boolean;
  yearRangeMin = new Date().getFullYear() - 100;
  yearRangeMax = new Date().getFullYear() + 3;
  guid = generateGuid();
  error = null;

  ngOnInit() {
    this.disableDateFromCheck = (this.checkboxModel && this.disableDateOnCheck) ? true : false;
    this.fr = {
      firstDayOfWeek: 1,
      dayNames: ['Dimanche', 'Lundi', 'Mardi', 'Mercredi', 'Jeudi', 'Vendredi', 'Samedi'],
      dayNamesShort: ['Dim', 'Lun', 'Mar', 'Mer', 'Jeu', 'Ven', 'Sam'],
      dayNamesMin: ['Di', 'Lu', 'Ma', 'Me', 'Je', 'Ve', 'Sa'],
      monthNames: [
        'Janvier', 'Février', 'Mars', 'Avril', 'Mai', 'Juin', 'Juillet',
        'Août', 'Septembre', 'Octobre', 'Novembre', 'Décembre'],
      monthNamesShort: ['Jan', 'Fév', 'Mar', 'Avr', 'Mai', 'Jun', 'Jul', 'Aoû', 'Set', 'Oct', 'Nov', 'Déc'],
      today: `Aujourd'hui`,
      clear: 'Annuler',
      dateFormat: 'dd/mm/yy',
      weekHeader: 'Sem'
    };
  }

  checkboxChanged(event: boolean): void {
    this.checkboxValueChanged.emit(event);
    if (event === true && this.disableDateOnCheck) {
      this.modelDate = null;
      this.disableDateFromCheck = true;
      this.ngModelChange.emit(this.modelDate);
    } else {
      this.disableDateFromCheck = false;
    }
  }

  onDateChange(date: Date): void {
    if (!date || this.showTime) {
      this.ngModelChange.emit(date);
    } else {
      const utcDate = new Date(Date.UTC(date.getFullYear(), date.getMonth(), date.getDate()));
      this.ngModelChange.emit(utcDate);
    }
  }
}
