import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';

import { SelectItem } from 'primeng/api';
import { isEqual } from 'lodash';

import { generateGuid } from '../../shared/guid';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'anef-input-select-two-way-binding',
  templateUrl: './input-select-two-way-binding.component.html',
  styleUrls: ['./input-select-two-way-binding.component.scss'],
})
export class InputSelectTwoWayBindingComponent implements OnInit {
  error = null;

  @Input() model: any;
  @Input() required = false;
  @Input() showDeleteItemsButton = false;
  @Input() readonly = false;
  @Input() disabled = false;
  @Input() label: string;
  @Input() placeholder: string;
  @Input() options: SelectItem[] = [];
  @Input() filter = false;
  @Input() set errors(errors: string | Array<string>) {
    if (!errors) {
      this.error = null;
    } else if (typeof(errors) === 'string') {
      this.error = [errors];
    } else {
      this.error = errors;
    }
  }
  @Output() ngModelChange = new EventEmitter();

  guid = generateGuid();
  colSize = '12';

  ngOnInit() {
    this.colSize = this.showDeleteItemsButton ? '11' : '12';
  }

  onModelChange(): void {
    this.ngModelChange.emit(this.model);
  }

  toString(): string {
    if (!this.options) {
      return '-';
    }
    const selectedOption = this.options.find(option => isEqual(this.model, option.value));
    return selectedOption ? selectedOption.label : '-';
  }

  deleteItem(): void {
    this.model = null;
    this.ngModelChange.emit(this.model);
  }
}
