import { Component, Input, Output, EventEmitter } from '@angular/core';
import { SelectItem } from 'primeng/api';
import { generateGuid } from '../../shared/guid';


@Component({
  // tslint:disable-next-line: component-selector
  selector: 'anef-input-select-button-two-way-binding',
  templateUrl: './input-select-button-two-way-binding.component.html',
  styleUrls: ['./input-select-button-two-way-binding.component.scss']
})
export class InputSelectButtonTwoWayBindingComponent {

  error = null;

  @Input() model: boolean;
  @Input() readonly: boolean;
  @Input() required: string;
  @Input() label: string;
  @Input() multiple = false;
  @Input() options: SelectItem[];
  @Input() set errors(errors: string | Array<string>) {
    if (!errors) {
      this.error = null;
    } else if (typeof (errors) === 'string') {
      this.error = [errors];
    } else {
      this.error = errors;
    }
  }
  @Output() ngModelChange = new EventEmitter();

  guid = generateGuid();

  toString(): string {
    if (!this.options) {
      return '-';
    }
    const selectedOption = this.options.find(option => this.model === option.value);
    return selectedOption ? selectedOption.label : '-';
  }
}
