import { Component, Input, Output, EventEmitter } from '@angular/core';

import { generateGuid } from '../../shared/guid';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'anef-input-multi-text-two-way-binding',
  templateUrl: './input-multi-text-two-way-binding.component.html',
  styleUrls: ['./input-multi-text-two-way-binding.component.scss'],
})
export class InputMultiTextTwoWayBindingComponent {
  private readonly MAX_LENGTH: number = 524288;
  error = null;

  @Input('model') set model(value) {
    this.value = value || [];
  }
  @Input() type = 'text';
  @Input() required = false;
  @Input() readonly = false;
  @Input() capitalize = false;
  @Input() label: string;
  @Input() placeholder: string;
  @Input() maxLength = this.MAX_LENGTH;
  @Input() addItemLabel: string;
  @Input() set errors(errors: string | Array<string>) {
    if (!errors) {
      this.error = null;
    } else if (typeof(errors) === 'string') {
      this.error = [errors];
    } else {
      this.error = errors;
    }
  }
  @Output() ngModelChange = new EventEmitter();

  guid = generateGuid();
  value: any[] = [];

  onModelChange(): void {
    if (this.capitalize) {
      this.value = this.value.map(element => this._capitalize(element));
    }
    if (this.value[0] === '' && this.value.length === 1) {
      this.value = null;
    }
    this.ngModelChange.emit(this.value);
  }

  addItem(): void {
    this.value.push('');
  }

  deleteItem(index: number): void {
    this.value.splice(index, 1);
  }

  toString(item: string): string {
    return item ? item : '-';
  }

  trackByFn(index: number, item: string): number {
    return index;
  }

  private _capitalize(text: string): string {
    return (text.charAt(0).toUpperCase() + text.slice(1));
  }
}
