import { Component, ContentChild, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, TemplateRef } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, FormGroupDirective, Validators } from '@angular/forms';

@Component({
  selector: 'anef-repeat-form',
  templateUrl: './repeat-form.component.html',
  styleUrls: ['./repeat-form.component.scss']
})
export class RepeatFormComponent implements OnInit, OnChanges {
  @ContentChild(TemplateRef) template: TemplateRef<any>;
  @Input() readOnly = false;
  @Input() required = true;
  @Input() maxNumber: number;
  @Input() minNumber: number = 1;
  @Input() inputNumberLabel = '';
  @Input() removeLabel = '';
  @Input() confirmLabel = '';
  @Input() errorMessages = {
    required: 'Ce champ est obligatoire',
    pattern: 'Le format n\'est pas correct',
    min: 'Le nombre de répétition n\'est pas assez grand',
    max: 'Le nombre de répétition est trop grand'
  };
  @Input() controlName: string;
  @Input() header = '';
  @Input() isFormTouched = false;
  @Input() entreprisesSizeVide: boolean = false;

  @Output() addedForm: EventEmitter<FormGroup> = new EventEmitter<FormGroup>();
  @Output() addedFormZero: EventEmitter<any> = new EventEmitter();
 

  numberRegexPattern: RegExp = /^\d+$/;

  form: FormArray;
  formSize: FormGroup;
  isMinZeroReadOnly: boolean = false;

  constructor(private parentForm: FormGroupDirective,
              private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.initForm();
    this.patchForm();
    this.patchFormSizeVide();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this.isFormTouched) {
      this.formSize.controls['SIZE'].markAsTouched()
    }
  }

  private initForm() {
    this.form = this.parentForm.form.get(this.controlName) as FormArray;
    const validators = this.buildSizeValidators();
    this.formSize = this.formBuilder.group({
      SIZE: ['', validators]
    });
    if (this.readOnly) {
      this.formSize.disable();
    }
  }

  private patchForm() {
    const initialFormSize = this.form.length === 0 ? '' : this.form.length;
    this.formSize.get('SIZE').setValue(initialFormSize);
    if (initialFormSize) {
      this.confirmSize();
    } 
  }

  private patchFormSizeVide() {
    if (this.minNumber == 0 && this.entreprisesSizeVide) {
      const initialFormSize = this.form.length;
      this.formSize.get('SIZE').setValue('0');
      this.isMinZeroReadOnly = true;
    }
  }

  private buildSizeValidators(): Validators[] {
    let validators: Validators[] = [Validators.pattern(this.numberRegexPattern)];
    if (this.required) {
      validators.push(Validators.required);
    }
    if (this.minNumber) {
      validators.push(Validators.min(this.minNumber));
    } 
    if (this.maxNumber && this.maxNumber > 0) {
      validators.push(Validators.max(this.maxNumber));
    }
    return validators;
  }

  confirmSize() {
    this.formSize.get('SIZE').markAsTouched();
    if (this.formSize.valid) {
      while(! this.isMaxSizeReached() && !this.isMinSizeZero()) {
        this.addElement();
      }

      if (this.isMinSizeZero()) {
        this.isMinZeroReadOnly = true;
        this.addedFormZero.emit(true);
      }
    }
  }

  private addElement() {
    const newForm = this.formBuilder.group({});
    this.form.push(newForm);
    this.addedForm.emit(newForm);
  }

  clearAllElements() {
    this.resetFormIsMinSizeZero();
    while (this.form.length > 0) {
      this.form.removeAt(0);
    }
  }

  private resetFormIsMinSizeZero() {
    if (this.isMinSizeZero()) {
      this.isMinZeroReadOnly = false;
      this.formSize.get('SIZE').reset();
    }
  }

  isMaxSizeReached(): boolean {
    if (this.isMinZeroReadOnly) {
      return true;
    }
    return this.formSize.valid && this.size >= this.formSize.get('SIZE').value && this.formSize.get('SIZE').value > 0;
  }

  get size() {
    return this.form.length;
  }

  isMinSizeZero(): boolean {
    return this.minNumber == 0 && parseInt(this.formSize.get('SIZE').value, 10) === 0;
  }
}
