import {OnDestroy, Output, EventEmitter, Input, Component, OnInit} from '@angular/core';

@Component({
    selector: 'lib-generic-modal',
    templateUrl: './generic-modal.component.html',
    styleUrls: ['./generic-modal.component.css']
})
export class GenericModalComponent implements OnInit, OnDestroy {
    @Input() displayGenericModal: boolean;
    @Input() title: string;
    @Input() hideCloseBtn: boolean;
    @Input() hideConfirmBtn: boolean;
    @Input() closeBtn: string;
    @Input() confirmBtn: string;
    @Input() modalClass: string;
    @Input() confirmBtnClass: string;
    @Input() closeBtnClass: string;
    @Input() disableConfirmBtn: boolean;
    @Input() modalWidth = 60;
    @Output() displayGenericModalChange = new EventEmitter();

    width: string;

    ngOnInit() {
      this.width = this.modalWidth + 'rem';
    }

    onClose() {
        this.displayGenericModalChange.emit(false);
    }

    onSubmit() {
        this.displayGenericModalChange.emit(true);
    }

    ngOnDestroy() {
        this.displayGenericModalChange.unsubscribe();
    }
}
